const withSwapiService = (Wrapped, mapMethodsToProps) => {
  return (props) => {
    return (
      <SwapiServiceContext.Consumer>
        {
          (swapi) => {
            const apiProps = mapMethodsToProps(swapi);
            return <Wrapped {...props} {...apiProps} />
          }
        }
      </SwapiServiceContext.Consumer>
    );
  }
}

export default withSwapiService;